/* Wrap Menu */
.wrap-menu {

}
.wrap-main > .card > .card-header {
  padding-left: 0px !important;
  padding-right: 0px;
}
.wrap-menu .list-group-item {
  padding: 0px;
}
.wrap-menu a {
  display: block;
  padding: 0.75rem 1.25rem;
  color: #f58220;
  font-weight: 600;
  text-transform: uppercase;
}

/* Top Nav */
.topnav {
  display: inline-block;
  margin-right: 15px;
  padding: 5px;
  /* color: #f58220; */
  color: #333;
  text-align: center;
}
.topnav u {
  text-decoration: none;
}
.topnav.active {
  background-color: #f1f1f1;
}
.topnav.active u {
  border-bottom: 2px solid #ac051f;
}
.topnav > img {
  margin-right: 5px;
}
.topnav > p {
  display: inline;
  font-size: 0.86rem;
  vertical-align: bottom;
}
.topnav:hover {
  background-color: #f1f1f1;
  text-decoration: none;
}

